@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Inter';
  background-color: #f2f2f2;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.add-to-cart-btn {
  width: 100%;
}

.add-to-cart-btn a {
  width: 100%;
  display: flex;
  padding: 12px 0;
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
  border-radius: 5px;
  align-items: center;
  background: #40B25C;
  text-decoration: none;
  justify-content: center;
  text-transform: uppercase;
}

.add-to-cart-btn a:hover {
  background: #1C6C3E;
  transition: background-color 0.5s ease;
}
.shelf-item {
  width: 304px;
  padding: 32px;
  display: flex;
  border-radius: 8px;
  align-items: center;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
}

.prodcut-image {
  width: 240px;
  height: 240px;
  margin: 0 0 32px;
}

.product-name {
  margin-bottom: 24px;
}

.product-name .name {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  color: #1C1C1C;
  line-height: 20px;
}

.product-price {
  width: 100%;
  display: flex;
  margin-bottom: 32px;
  flex-direction: column;
  align-items: flex-start;
}

.product-price .price {
  margin: 0;
  font-size: 14px;
  color: #b5b5b6;
  font-weight: 500;
  line-height: 20px;
  text-decoration: line-through;
}

.product-price .list-price {
  margin: 4px 0;
  font-size: 20px;
  font-weight: 600;
  color: #da4b4f;
  line-height: 20px;
}

.product-price .product-installments {
  margin: 4px 0;
  font-size: 12px;
  font-weight: 500;
  color: #1C1C1C;
}

.product-price .product-installments span {
  color: #848587;
}
.product-showcase {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
