.add-to-cart-btn {
  width: 100%;
}

.add-to-cart-btn a {
  width: 100%;
  display: flex;
  padding: 12px 0;
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
  border-radius: 5px;
  align-items: center;
  background: #40B25C;
  text-decoration: none;
  justify-content: center;
  text-transform: uppercase;
}

.add-to-cart-btn a:hover {
  background: #1C6C3E;
  transition: background-color 0.5s ease;
}